import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams, ViewController, ModalController, Events, AlertController } from 'ionic-angular';
import { HttpProvider } from '../../providers/http/http'
import { AlertProvider } from '../../providers/alert/alert'
import { AuthProvider } from '../../providers/auth/auth'
import { LoadingProvider } from '../../providers/loading/loading'
import { MapSearchPage } from '../map-search/map-search'

@Component({
  selector: 'page-add-modal',
  templateUrl: 'add-modal.html',
})
export class AddModalPage {

  @ViewChild("detail_address") detail_address
  @ViewChild("place_name") place_name
  @ViewChild("place_description") place_description
  @ViewChild("place_operation") place_operation
  place_category = " "
  smoking_place_type = ""
  no_smoking_place_type = ""
  searchAddressDone = true;
  address_result = [];
  address_text = "";
  location

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public viewCtrl: ViewController,
    public http: HttpProvider,
    public alertCtrl: AlertProvider,
    public modalCtrl: ModalController,
    public events: Events,
    public AlertCtrl: AlertController,
    public auth: AuthProvider,
    public loadingCtrl: LoadingProvider
  ) {
    if (this.navParams.get("address")) {
      this.address_text = this.navParams.get("address")
      this.location = this.navParams.get("location")

      this.selectAddress(this.address_text)
    }

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddModalPage');
  }

  ionViewWillLeave() {

  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

  deleteAddress(e) {
    this.searchAddressDone = false;
    this.address_text = "";
    this.address_result = [];
  }

  inputAddress(e) {
    var input = e.target.value;
    console.log(input)
  }

  searchAddress() {
    if (this.address_text == "") {
      this.alertCtrl.showAlert(" .")
    } else {
      this.http.searchGeoLocation(this.address_text)
        .then((res: any) => {
          this.address_result = res.results

          let alert = this.AlertCtrl.create();
          alert.setTitle(' ');

          this.address_result.map((v, i) => {
            alert.addInput({
              type: 'radio',
              label: v.formatted_address.replace(" ", ""),
              value: "" + i,
              checked: true
            });
          })

          alert.addButton('');
          alert.addButton({
            text: '',
            handler: data => {
              this.selectAddress(this.address_result[data].formatted_address.replace(" ", ""))
            }
          });
          alert.present();

        }).catch(error => {
          this.alertCtrl.showAlert("  .")
        })
      // console.log(address_text)
    }
  }

  searchStart() {

  }

  selectAddress(selected_address) {
    this.searchAddressDone = true;
    this.address_text = selected_address;
  }

  // searchOnMap(){
  //   const modal = this.modalCtrl.create(MapSearchPage);
  //   modal.present();
  // }

  clickAdd() {
    console.log("", this.address_text)
    console.log("", this.detail_address.value)
    console.log("  ", this.place_category)
    console.log("  ", this.place_category == " " ? this.smoking_place_type : this.no_smoking_place_type)
    console.log(" ", this.place_name.value)
    console.log("  ", this.place_description.value)
    console.log(" ", this.place_operation.value)
    console.log(this.auth.user)
    // if (this.address_text == "") {
    //   this.alertCtrl.showAlert(" .")
    //   return;
    // }   

    if (this.place_name.value == "") {
      this.alertCtrl.showAlert(" .")

      return;
    }

    // if (this.place_description.value == "") {
    //   // this.alertCtrl.showAlert("   .")
    //   // return;
    // }

    this.alertCtrl.showConfirm("? (      .)", "", "", () => {
      if (this.auth.user) {
        this.loadingCtrl.present()
        this.auth.currentUser().getIdToken()
          .then((idToken) => {
            return this.http.addPlace({
              address1: this.address_text,
              address2: this.detail_address.value,
              category: this.place_category,
              description1: this.place_description.value == ""? null :  this.place_description.value,
              editor: this.auth.user.nickname,
              password: this.auth.user.uid,
              latitude: this.location.lat,
              longitude: this.location.lng,
              name: this.place_name.value,
              operator: this.place_operation.value,
              type: this.place_category == " " ? this.smoking_place_type : this.no_smoking_place_type,
              idToken: idToken
            })

          }).then((res: any) => {
            this.events.publish("refresh")
            this.loadingCtrl.dismiss()
            this.alertCtrl.showAlert("  .    >    .")
            this.dismiss()
          }).catch(error => {
            this.loadingCtrl.dismiss()
            console.log(JSON.stringify(error))
            this.alertCtrl.showAlert("   .")
          })
      } 
    }, () => { })



  }

}



