import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams, Slides, ViewController } from 'ionic-angular';
import { AuthProvider } from '../../providers/auth/auth'

@Component({
  selector: 'page-intro',
  templateUrl: 'intro.html',
})

export class IntroPage {
  @ViewChild(Slides) slidesElement: Slides;
  IsSlideEnd = false;
  user = {
    uid: "",
    nickname: ""
  }
  slides = [];

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public viewCtrl: ViewController,
    public auth: AuthProvider
  ) {
    if (auth.user) this.user = auth.user
    else {
      this.user.uid = this.navParams.get("uid");
      this.user.nickname = this.navParams.get("nickname");
    }
  }

  ionViewDidLoad() {
    if (this.auth.user) this.user = this.auth.user
    else {
      this.user.uid = this.navParams.get("uid");
      this.user.nickname = this.navParams.get("nickname");
    }
    // this.slidesElement.lockSwipes(true);
    console.log('ionViewDidLoad IntroPage');
    this.slides = [{
      title: "",
      description: "    <b>' '</b>   ?  <b></b> <b></b> <b></b>   .",
      image: "./assets/imgs/smoke.png",
    },
    {
      title: "",
      description: "<b> </b>,      <b></b> .      . ",
      image: "./assets/imgs/map-point.png",
    },
    {
      title: this.user.nickname + " ",
      description: "<b></b>         .<br>  " + this.user.nickname + "    .      .   []  .",
      image: "./assets/imgs/shake.png",
    }]
  }

  closePage(e) {
    if (this.IsSlideEnd) {
      this.dismiss()
    } else {
      this.slidesElement.slideNext();
    }
  }

  ionSlideDidChange(e) {

    if (this.slidesElement.length && this.slidesElement.length() - 1 > this.slidesElement.getActiveIndex()) {
      this.IsSlideEnd = false;
    } else {
      this.IsSlideEnd = true;
    }
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

}



